/* Section/Article */

	section, article {
		&.special {
			text-align: center;
		}
	}

	#greeting header.major {
		// font-family: "Courier New";
		padding-top: 10px;
		color: white;
	}

	header {
		p {
			color: _palette(fg-light);
			position: relative;
			top: -0.25em;
		}

		.excerpt-inline {
			font-size: _font(hdr-size-xxsmall);
			font-weight: _font(weight);
		}

		h2 + p {
		}

		h3 + p {
			font-size: _font(hdr-size-large);
		}

		h4 + p,
		h5 + p,
		h6 + p {
			font-size: _font(hdr-size-smaller);
		}

		&.major {
		/*	margin: 0 0 (_size(element-margin) * 1.75) 0;*/

			h1, h2, h3, h4, h5, h6 {
				border-bottom: solid 2px _palette(border);
				display: inline-block;
				// padding-bottom: 1em;
				position: relative;

				&:after {
					content: '';
					display: block;
					height: 1px;
				}
			}

			p {
				color: _palette(fg);
				top: 0;
			}

			@include breakpoint(small) {
				margin: 0 0 _size(element-margin) 0;
			}
		}

		@include breakpoint(medium) {
			br {
				display: none;
			}
		}
	}

/* Form */

	form {
		margin: 0 0 _size(element-margin) 0;
	}

	label {
		color: _palette(fg-bold);
		display: block;
		font-size: _font(ctrl-form-label-size);
		font-weight: _font(weight-bold);
		margin: 0 0 (_size(element-margin) * 0.5) 0;
	}

	input[type="text"],
	input[type="password"],
	input[type="email"],
	select,
	textarea {
		@include vendor('appearance', 'none');
		background: _palette(border-bg);
		border-radius: 3px;
		border: none;
		color: inherit;
		display: block;
		outline: 0;
		padding: 0 1em;
		text-decoration: none;
		width: 100%;

		&:invalid {
			box-shadow: none;
		}

		&:focus {
			box-shadow: 0 0 0 2px _palette(accent1, bg);
		}
	}

	.select-wrapper {
		@include icon;
		display: block;
		position: relative;

		&:before {
			@include vendor('pointer-events', 'none');
			color: _palette(border);
			content: '\f078';
			display: block;
			height: _size(element-height);
			line-height: _size(element-height);
			position: absolute;
			right: 0;
			text-align: center;
			top: 0;
			width: _size(element-height);
		}

		select::-ms-expand {
			display: none;
		}
	}

	input[type="text"],
	input[type="password"],
	input[type="email"],
	select {
		height: _size(element-height);
	}

	textarea {
		padding: 0.75em 1em;
	}

	input[type="checkbox"],
	input[type="radio"], {
		@include vendor('appearance', 'none');
		display: block;
		float: left;
		margin-right: -2em;
		opacity: 0;
		width: 1em;
		z-index: -1;

		& + label {
			@include icon;
			color: _palette(fg);
			cursor: pointer;
			display: inline-block;
			font-size: _font(ctrl-form-size);
			font-weight: _font(weight);
			padding-left: (_size(element-height) * 0.6) + 0.75em;
			padding-right: 0.75em;
			position: relative;

			&:before {
				background: _palette(border-bg);
				border-radius: 3px;
				content: '';
				display: inline-block;
				height: (_size(element-height) * 0.6);
				left: 0;
				line-height: (_size(element-height) * 0.575);
				position: absolute;
				text-align: center;
				top: 0;
				width: (_size(element-height) * 0.6);
			}
		}

		&:checked + label {
			&:before {
				background: _palette(bg);
				color: _palette(fg-bold);
				content: '\f00c';
			}
		}

		&:focus + label {
			&:before {
				box-shadow: 0 0 0 2px _palette(accent1, bg);
			}
		}
	}

	input[type="checkbox"] {
		& + label {
			&:before {
				border-radius: 3px;
			}
		}
	}

	input[type="radio"] {
		& + label {
			&:before {
				border-radius: 100%;
			}
		}
	}

	::-webkit-input-placeholder {
		color: _palette(fg-light) !important;
		opacity: 1.0;
	}

	:-moz-placeholder {
		color: _palette(fg-light) !important;
		opacity: 1.0;
	}

	::-moz-placeholder {
		color: _palette(fg-light) !important;
		opacity: 1.0;
	}

	:-ms-input-placeholder {
		color: _palette(fg-light) !important;
		opacity: 1.0;
	}

	.formerize-placeholder {
		color: _palette(fg-light) !important;
		opacity: 1.0;
	}

/* Box */

	.box {
		border-radius: 3px;
		border: solid 2px _palette(border);
		margin-bottom: _size(element-margin);
		padding: 1.5em;

		> :last-child,
		> :last-child > :last-child,
		> :last-child > :last-child > :last-child {
			margin-bottom: 0;
		}

		&.alt {
			border: 0;
			border-radius: 0;
			padding: 0;
		}
	}

/* Icon */

	.icon {
		@include icon;
		border-bottom: none;
		position: relative;

		> .label {
			display: none;
		}

		&.major {
			@include vendor('transform', 'rotate(-45deg)');
			border-radius: 3px;
			border: solid 2px _palette(border);
			display: inline-block;
			font-size: _font(hdr-size-xlarge);
			height: calc(3em + 2px);
			line-height: 3em;
			text-align: center;
			width: calc(3em + 2px);

			&:before {
				@include vendor('transform', 'rotate(45deg)');
				display: inline-block;
				font-size: 1.5em;
			}

			@include breakpoint(small) {
				font-size: _font(hdr-size-large);
			}
		}

		&.style1 {
			color: _palette(accent2, bg);
		}

		&.style2 {
			color: _palette(accent3, bg);
		}

		&.style3 {
			color: _palette(accent4, bg);
		}

		&.styleN {
			font-size: _font(hdr-size-xxlarge);
			&.Nvar0 {
				color: _palette(accent7, fg-light);
			}
			&.Nvar1 {
				color: _palette(accent7, fg-light);
			}
			&.Nvar2 {
				color: _palette(accent7, fg-light);
			}
			&.Nvar3 {
				color: _palette(accent6, bg);
			}
		}
	}

/* Image */
 
	.image {
		border-radius: 3px;
		border: 0;
		display: inline-block;
		position: relative;

		img {
			border-radius: 3px;
			display: block;
		}

		&.left {
			float: left;
			margin: 0 2em 2em 0;
			top: 0.25em;
		}

		&.right {
			float: right;
			margin: 0 0 2em 2em;
			top: 0.25em;
		}

		&.note {
			float: left;
			margin: 0 1em 0 0;
			top: 0.25em;
		}

		&.left,
		&.right,
		&.note {
			max-width: 40%;

			img {
				width: 100%;
			}
		}

		&.fit {
			display: block;
			margin: 0 0 _size(element-margin) 0;
			width: 100%;

			img {
				width: 100%;
			}
		}
	}

/* List */

	ol {
		list-style: decimal;
		margin: 0 0 _size(element-margin) 0;
		padding-left: 1.25em;

		li {
			padding-left: 0.25em;
		}
	}

	ul {
		list-style: disc;
	/*	margin: 0 0 _size(element-margin) 0em;*/
		padding-left: 1em;
		margin: 0 0 0em 0em;


		li {
			padding-left: 0.5em;
		}

		&.alt {
			list-style: none;
			padding-left: 0;

			li {
				border-top: solid 1px _palette(border);
				padding: 0.5em 0;

				&:first-child {
					border-top: 0;
					padding-top: 0;
				}
			}
		}

		&.icons {
			cursor: default;
			list-style: none;
			padding-left: 0;

			li {
				display: inline-block;
				padding: 0 1em 0 0;

				&:last-child {
					padding-right: 0 !important;
				}
			}

			&.major {
				padding: 1em 0;
				padding: 0em 0;

				li {
					padding-right: 3.5em;

					@include breakpoint(small) {
						padding: 0 1em !important;
					}
				}
			}
		}

		&.actions {
			cursor: default;
			list-style: none;
			padding-left: 0;

			li {
				display: inline-block;
				padding: 0 (_size(element-margin) * 0.75) 0 0;
				vertical-align: middle;

				&:last-child {
					padding-right: 0;
				}
			}

			&.small {
				li {
					padding: 0 (_size(element-margin) * 0.375) 0 0;
				}
			}

			&.vertical {
				li {
					display: block;
					padding: (_size(element-margin) * 0.75) 0 0 0;

					&:first-child {
						padding-top: 0;
					}

					> * {
						margin-bottom: 0;
					}
				}

				&.small {
					li {
						padding: (_size(element-margin) * 0.375) 0 0 0;

						&:first-child {
							padding-top: 0;
						}
					}
				}
			}

			&.fit {
				display: table;
				margin-left: (_size(element-margin) * -0.75);
				padding: 0;
				table-layout: fixed;
				width: calc(100% + #{(_size(element-margin) * 0.75)});

				li {
					display: table-cell;
					padding: 0 0 0 (_size(element-margin) * 0.75);

					> * {
						margin-bottom: 0;
					}
				}

				&.small {
					margin-left: (_size(element-margin) * -0.375);
					width: calc(100% + #{(_size(element-margin) * 0.375)});

					li {
						padding: 0 0 0 (_size(element-margin) * 0.375);
					}
				}
			}

			@include breakpoint(small) {
				li {
					display: block;
					padding: (_size(element-margin) * 0.5) 0 0 0;
					text-align: center;
					width: 100%;

					&:first-child {
						padding-top: 0;
					}

					> * {
						margin: 0 auto !important;
						max-width: 30em;
						width: 100%;

						&.icon {
							&:before {
								margin-left: -1em;
							}
						}
					}
				}

				&.small {
					li {
						padding: (_size(element-margin) * 0.25) 0 0 0;

						&:first-child {
							padding-top: 0;
						}
					}
				}
			}
		}
	}

