	table {
		margin: 0 0 _size(element-margin) 0;
		width: 100%;

		tbody {
			tr {
				border: solid 1px _palette(border);
				border-left: 0;
				border-right: 0;

				&:nth-child(2n + 1) {
					background-color: _palette(border-bg);
				}
			}
		}

		td {
			padding: 0.75em 0.75em;
		}

		th {
			color: _palette(fg-bold);
			font-size: 0.9em;
			font-weight: _font(weight-bold);
			padding: 0 0.75em 0.75em 0.75em;
			text-align: left;
		}

		thead {
			border-bottom: solid 2px _palette(border);
		}

		tfoot {
			border-top: solid 2px _palette(border);
		}

		&.alt {
			border-collapse: separate;

			tbody {
				tr {
					td {
						border: solid 1px _palette(border);
						border-left-width: 0;
						border-top-width: 0;

						&:first-child {
							border-left-width: 1px;
						}
					}

					&:first-child {
						td {
							border-top-width: 1px;
						}
					}
				}
			}

			thead {
				border-bottom: 0;
			}

			tfoot {
				border-top: 0;
			}
		}
	}

	@include breakpoint(small) {
	// from https://css-tricks.com/responsive-data-tables/
		/* Force table to not be like tables anymore */
		table, thead, tbody, th, td, tr {
			display: block;
		}
		/* Hide table headers (but not display: none;, for accessibility) */
		thead tr {
			position: absolute;
			top: -9999px;
			left: -9999px;
		}
		//tr { border: 1px solid #ccc; }
		td {
			/* Behave  like a "row" */
			border: none;
			//border-bottom: 1px solid #eee;
			position: relative;
			padding-left: 50%;
		}
	}

/* Button */

	input[type="submit"],
	input[type="reset"],
	input[type="button"],
	button,
	.button {
		@include vendor('appearance', 'none');
		@include vendor('transition', ('background-color #{_duration(transitions)} ease-in-out', 'color #{_duration(transitions)} ease-in-out'));
		background-color: transparent;
		border-radius: 3px;
		border: 0;
		box-shadow: inset 0 0 0 2px _palette(border);
		// color: _palette(fg-bold);
		color: rgb(200,200,200);
				cursor: pointer;
		display: inline-block;
		font-size: _font(ctrl-bttn-size);
		font-weight: _font(weight-bold);
		height: 3.125em;
		letter-spacing: _size(letter-spacing-alt);
		line-height: _font(ctrl-bttn-lheight);
		padding: 0 2.75em;
		text-align: center;
		text-decoration: none;
		text-transform: uppercase;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;

		&:hover {
			background-color: _palette(border-bg);
		}

		&:active {
			background-color: _palette(border2-bg);
		}

		&.icon {
			&:before {
				margin-right: 0.5em;
			}
		}

		&.fit {
			display: block;
			margin: 0 0 (_size(element-margin) * 0.5) 0;
			width: 100%;
		}

		&.small {
			font-size: _font(ctrl-bttn-size);
		}

		&.big {
			font-size: _font(ctrl-bttn-bigsize);
		}

		&.special {
			background-color: _palette(accent6, bg);
			box-shadow: none !important;
			color: _palette(accent6, fg-bold) !important;

			&:hover {
				background-color: lighten(_palette(accent6, bg), 5) !important;
			}

			&:active {
				background-color: darken(_palette(accent6, bg), 5) !important;
			}
		}

		&.disabled,
		&:disabled {
			@include vendor('pointer-events', 'none');
			opacity: 0.25;
		}

		@include breakpoint(small) {
			height: 3.75em;
			line-height: 3.75em;
		}
	}

/* Features */

	.features {
		@include vendor('display', 'flex');
		@include vendor('flex-wrap', 'wrap');
		@include vendor('justify-content', 'center');
		list-style: none;
		padding: 0;
		width: 100%;

		a {
			@include vendor('transition', ('color #{_duration(transitions)} ease', 'border-bottom-color #{_duration(transitions)} ease'));
			border-bottom: none;
			color: rgb(200,200,200);
						text-decoration: none;

			&:hover {
				border-bottom-color: none;
			}
		}

		li {
			display: block;
			// position: relative;
			text-align: left;
			width: 50%;

			.container {
	  		width: 100%;
  			height: 100%;
 				margin: 0;
				//  background-color: red;
			padding: 2em 2em 2em 4em;
  			// @include padding(4em, 4em, (0,0,0,2em));
			}

			@for $i from 1 through _misc(max-features) {
				$j: 0.035 * $i;

				&:nth-child(#{$i}) {
					background-color: rgba(0,0,0, $j);
				}
			}

			&:before {
				display: block;
				color: _palette(accent2, bg);
				position: absolute;
				left: 1.75em;
				top: 2.75em;
				font-size: 1.5em;
			}

			&:nth-child(1) {
				border-top-left-radius: 3px;
			}

			&:nth-child(2) {
				border-top-right-radius: 3px;
			}

			&:nth-last-child(1) {
				border-bottom-right-radius: 3px;
			}

			&:nth-last-child(2) {
				border-bottom-left-radius: 3px;
			}

			@include breakpoint(medium) {
				@include padding(3em, 2em);
				text-align: center;

				&:before {
					left: 0;
					margin: 0 0 (_size(element-margin) * 0.5) 0;
					position: relative;
					top: 0;
				}
			}

			@include breakpoint(small) {
				@include padding(3em, 0);
				background-color: transparent !important;
				border-top: solid 2px _palette(border);
				width: 100%;

				&:first-child {
					border-top: 0;
				}
			}
		}
	}

/* Spotlight */

	.spotlight {
		// background-color: black;
		background-color: rgba(30, 32, 45, 0.75);
   	@include vendor('align-items', 'center');
 		@include vendor('display', 'flex');

		a {
			padding:0;
			margin:0;
			@include vendor('transition', ('color #{_duration(transitions)} ease', 'border-bottom-color #{_duration(transitions)} ease'));
			border-bottom: none;
			 color: rgb(200,200,200);;
			text-decoration: none;

			&:hover {
				border-bottom-color: none;
			}
		}

		.image {
			@include vendor('order', '1');
			border-radius: 0;
			width: 40%;
			img {
				border-radius: 0;
				width: 100%;
			}
		}

		.content {
			@include padding(2em, 2em);
			@include vendor('order', '2');
		}
		.content-wrap {
			// width: 60%;
			width: 80%;
		}
		&:nth-child(1n) {
			text-align: right;
		}
		&:nth-child(2n) {
			@include vendor('flex-direction', 'row-reverse');
			text-align: left;
		}

		@for $i from 1 through _misc(max-spotlights) {
			$j: 0.075 * $i;
			&:nth-child(#{$i}) {
			/*	background-color: rgba(0,0,0, $j);*/
			// background-color: rgba(0,0,0,1);
			background-color: rgba(30, 32,45, 0.75);
			}
		}

		@include breakpoint(large) {
			.image {
				// width: 45%;
				width: 20%;

			}
			.content-wrap {
								// width: 60%;

				width: 80%;
			}
		}

		@include breakpoint(medium) {
			display: block;
			br {
				display: none;
			}
			.image {
				width: 100%;
			}
			.content {
				@include padding(4em, 3em);
				max-width: none;
				text-align: center;
			}
			.content-wrap {
				width: 100%;
			}
		}

		@include breakpoint(small) {
			.content {
				@include padding(3em, 2em);
			}
		}

	}

/* Wrapper */

	@mixin wrapper($p) {
		/*background-color: _palette($p, bg);*/
		background-color: rgba(0,0,0,0.8);
		color: rgb(200,200,200);
		// Basic

			strong, b {
				// color: _palette($p, fg-bold);
				color: rgb(200,200,200);
							//	text-decoration-line: underline;
			}

			h2, h3, h4, h5, h6 {
				color: white;
			}

			hr {
				border-color: _palette($p, border);
			}

			blockquote {
				border-color: _palette($p, border);
			}
			/*
			code {
				background: _palette($p, border-bg);
			}
			*/

///////////////////// affected the HI hello welcome portion

		// Section/Article

			header {
				p {
				// font-family: "Courier New";
					// background-color: black;

					color: rgb(200,200,200);
				}

				&.major {
					h1, h2, h3, h4, h5, h6 {
						border-color: _palette($p, border);
						color: rgb(200,200,200);}

/////////// The actual description text
					p {
						color: rgb(200,200,200);

					}
				}
			}

		// Form

			label {
				color: _palette($p, fg-bold);
			}

			input[type="text"],
			input[type="password"],
			input[type="email"],
			select,
			textarea {
				background: _palette($p, border-bg);
			}

			.select-wrapper {
				&:before {
					color: rgb(200,200,200);
				}
			}

			input[type="checkbox"],
			input[type="radio"], {
				& + label {
					color: _palette($p, fg);

					&:before {
						background: _palette($p, border-bg);
					}
				}

				&:checked + label {
					&:before {
						background: _palette($p, fg-bold);
						color: _palette($p, bg);
					}
				}
			}

			::-webkit-input-placeholder {
				color: _palette($p, fg-light) !important;
			}

			:-moz-placeholder {
				color: _palette($p, fg-light) !important;
			}

			::-moz-placeholder {
				color: _palette($p, fg-light) !important;
			}

			:-ms-input-placeholder {
				color: _palette($p, fg-light) !important;
			}

			.formerize-placeholder {
				color: _palette($p, fg-light) !important;
			}

		// Icon

			.icon {
				&.major {
					border-color: _palette($p, border);
				}
			}

		// List

			ul {
				&.alt {
					li {
						border-color: _palette($p, border);
					}
				}
			}

		// Table

			table {
				tbody {
					tr {
						border-color: _palette($p, border);

						&:nth-child(2n + 1) {
							background-color: _palette($p, border-bg);
						}
					}
				}

				th {
					// color: _palette($p, fg-bold);
					color: rgb(200,200,200);
				}

				thead {
					border-color: _palette($p, border);
				}

				tfoot {
					border-color: _palette($p, border);
				}

				&.alt {
					tbody {
						tr {
							td {
								border-color: _palette($p, border);

								&.Nvar0 {
									border-color: _palette(accent3, border);
									color: _palette(accent7, fg);
									background-color: _palette(accent3, bg);
								}
								&.Nvar1 {
									border-color: _palette(accent2, border);
									color: _palette(accent7, fg);
									background-color: _palette(accent2, bg);
								}
								&.Nvar2 {
									border-color: _palette(accent7, border2);
									color: _palette(accent7, fg-bold);
									background-color: _palette(accent7, border);
								}
								&.Nvar3 {
									border-color: _palette(accent6, bg);
									color: black; /*_palette(accent6, fg-bold);*/
									background-color: _palette(accent6, fg-light);
								}
							}
						}
					}
				}
			}

		// Button

			input[type="submit"],
			input[type="reset"],
			input[type="button"],
			button,
			.button {
				box-shadow: inset 0 0 0 2px _palette($p, border);
				// color: _palette($p, fg-bold);
				color: rgb(200,200,200);

				&:hover {
					background-color: _palette($p, border-bg);
				}

				&:active {
					background-color: _palette($p, border2-bg);
				}
			}

		// Features

			.features {
				li {
					@include breakpoint(small) {
						border-top-color: _palette($p, border);
					}
				}
			}

