/* Page Wrapper + Menu */

	#page-wrapper {
		@include vendor('transition', 'opacity #{_duration(menu)} ease');
		opacity: 1;
		padding-top: 3em;

		&:before {
			background: rgba(0,0,0,0);
			content: '';
			display: block;
			display: none;
			height: 100%;
			left: 0;
			position: fixed;
			top: 0;
			width: 100%;
			z-index: _misc(z-index-base) + 1;
		}
	}

	#menu {
		@include vendor('transform', 'translateX(20em)');
		@include vendor('transition', 'transform #{_duration(menu)} ease');
		-webkit-overflow-scrolling: touch;
		/* background: _palette(accent1, bg); */
		background: rgb(0,0,0);
		color: _palette(accent1, fg-bold);
		height: 100%;
		max-width: 80%;
		overflow-y: auto;
		padding: 3em 2em;
		position: fixed;
		right: 0;
		top: 0;
		width: 20em;
		z-index: _misc(z-index-base) + 2;

		ul {
			list-style: none;
			padding: 0;

			> li {
				border-top: solid 1px _palette(accent1, border);
				margin: 0.5em 0 0 0;
				padding: 0.5em 0 0 0;
				font-family: _font(ctrl-family);
				// font-family: "Courier New";

				&:first-child {
					border-top: 0 !important;
					margin-top: 0 !important;
					padding-top: 0 !important;
				}

				> a {
					border: 0;
					color: rgb(200,200,200);
					// color: inherit;
					display: block;
					font-size: _font(hdr-size-xsmall);
					letter-spacing: _size(letter-spacing-alt);
					outline: 0;
					text-decoration: none;
					text-transform: uppercase;

					@include breakpoint(small) {
						line-height: 3em;
					}
				}
			}
		}

		.close {
			background-image: url('images/close.svg');
			background-position: 4.85em 1em;
			background-repeat: no-repeat;
			border: 0;
			cursor: pointer;
			display: block;
			height: 3em;
			position: absolute;
			right: 0;
			top: 0;
			vertical-align: middle;
			width: 7em;
		}

		@include breakpoint(small) {
			padding: 3em 1.5em;
		}
	}

	body.is-menu-visible {
		#page-wrapper {
			opacity: 0.35;

			&:before {
				display: block;
			}
		}

		#menu {
			@include vendor('transform', 'translateX(0)');
		}
	}

/* Header */

	#header {
		@include vendor('transition', 'background-color #{_duration(transitions)} ease');
		/*background: _palette(bg);*/
			background-color: black;

		height: 3em;
		left: 0;
		line-height: 3em;
		position: fixed;
		top: 0;
		width: 100%;
		z-index: _misc(z-index-base);

		h1 {
			@include vendor('transition', 'opacity #{_duration(transitions)} ease');
			height: inherit;
			left: 1.25em;
			line-height: inherit;
			position: absolute;
			top: 0;
			text-transform: none;

			a {
				border: 0;
				display: block;
				height: inherit;
				line-height: inherit;

				@include breakpoint(small) {
					font-size: _font(hdr-size-xsmall);
				}
			}
		}

		nav {
			height: inherit;
			line-height: inherit;
			position: absolute;
			right: 0;
			top: 0;

			> ul {
				list-style: none;
				margin: 0;
				padding: 0;
				white-space: nowrap;

				> li {
					display: inline-block;
					padding: 0;

					> a {
						border: 0;
						color: _palette(fg-bold);
						display: block;
						font-size: _font(hdr-size-xsmall);
						letter-spacing: _size(letter-spacing-alt);
						padding: 0 1.5em;
						text-transform: uppercase;

						&.menuToggle {
							outline: 0;
							position: relative;

							&:after {
								background-image: url('images/bars.svg');
								background-position: right center;
								background-repeat: no-repeat;
								content: '';
								display: inline-block;
								height: 3.75em;
								vertical-align: top;
								width: 2em;
							}

							@include breakpoint(small) {
								padding: 0 1.5em;

								span {
									display: none;
								}
							}
						}

						@include breakpoint(small) {
							padding: 0 0 0 1.5em;
						}
					}

					&:first-child {
						margin-left: 0;
					}
				}
			}
		}

		&.alt {
			// background: transparent;

			h1 {
				@include vendor('pointer-events', 'none');
				opacity: 0;
			}
		}
	}

/* Banner */

	#banner {
		@include vendor('display', 'flex');
		@include vendor('flex-direction', 'column');
		@include vendor('justify-content', 'center');
		cursor: default;
		height: 80vh;
		min-height: 35em;
		overflow: hidden;
		position: relative;
		text-align: center;

		h1 {
			@include vendor('transform', 'scale(1)');
		@include vendor('transition', ('transform 0.5s ease', 'opacity 0.5s ease'));
			// @include vendor('transition', ('transform 0.2s ease', 'opacity 0.2s ease'));
			display: inline-block;
			font-size: 1.75em;
			line-height: 1.75em;
			opacity: 1;
			padding: 0.35em 1em;
			position: relative;
			z-index: 1;
			text-transform: none;

			&:before, &:after {
			@include vendor('transition', 'width 0.85s ease');
			@include vendor('transition-delay', '0.25s');
				// @include vendor('transition', 'width 0.85s ease');
				// @include vendor('transition-delay', '0');
				background: _palette(fg-bold);
				content: '';
				display: block;
				height: 2px;
				position: absolute;
				width: 100%;
			}

			&:before {
				top: 0;
				left: 0;
			}

			&:after {
				bottom: 0;
				right: 0;
			}
		}

		p {
			letter-spacing: _size(letter-spacing-alt);
			text-transform: uppercase;

			a {
				color: inherit;
			}
		}

		.more {
			@include vendor('transition', ('transform 0.75s ease', 'opacity 0.75s ease'));
		@include vendor('transition-delay', '3.5s');
			// @include vendor('transition-delay', '0');
			@include vendor('transform', 'translateY(0)');
			border: none;
			bottom: 0;
			color: inherit;
			font-size: 0.8em;
			height: 8.5em;
			left: 50%;
			letter-spacing: _size(letter-spacing-alt);
			margin-left: -8.5em;
			opacity: 1;
			outline: 0;
			padding-left: _size(letter-spacing-alt);
			position: absolute;
			text-align: center;
			text-transform: uppercase;
			width: 16em;
			z-index: 1;

			&:after {
				background-image: url('images/arrow.svg');
				background-position: center;
				background-repeat: no-repeat;
				background-size: contain;
				bottom: 4em;
				content: '';
				display: block;
				height: 1.5em;
				left: 50%;
				margin: 0 0 0 -0.75em;
				position: absolute;
				width: 1.5em;
			}
		}

		&:after {
			@include vendor('pointer-events', 'none');
			@include vendor('transition', 'opacity #{_duration(fadein)} ease-in-out');
//		@include vendor('transition-delay', '1.25s');
			@include vendor('transition-delay', '0');
			content: '';
			background: _palette(bg);
			display: block;
			width: 100%;
			height: 100%;
			position: absolute;
			left: 0;
			top: 0;
			opacity: 0;
		}

		@include breakpoint(small) {
			@include padding(7em, 3em);
			height: auto;
			min-height: 0;

			h1 {
				font-size: 1.25em;
			}

			br {
				display: none;
			}

			.more {
				display: none;
			}
		}
	}

	body.is-loading {
		#banner {
			h1 {
				@include vendor('transform', 'scale(0.95)');
				opacity: 0;

				&:before, &:after {
					width: 0;
				}
			}

			.more {
				@include vendor('transform', 'translateY(8.5em)');
				opacity: 0;
			}

			&:after {
				opacity: 1;
			}
		}
	}

/* CTA */

	#cta {
		.inner {
			@include vendor('display', 'flex');
			max-width: 45em;

			header {
				@include vendor('order', '1');
				padding-right: 3em;
				width: 70%;

				p {
					color: inherit;
				}
			}

			.actions {
				@include vendor('order', '2');
				width: 30%;
			}

			@include breakpoint(medium) {
				display: block;
				text-align: center;

				header {
					padding-right: 0;
					width: 100%;
				}

				.actions {
					margin-left: auto;
					margin-right: auto;
					max-width: 20em;
					width: 100%;
				}
			}

			@include breakpoint(small) {
				.actions {
					max-width: none;
				}
			}
		}
	}

/* Articles */

	#standard {
		> header {
			text-align: center;

			h1, h2 {
				font-size: _font(hdr-size-xxlarge);
				margin: 0 0 (_size(element-margin) * 0.25) 0;
			}

			p {
				color: inherit;
				letter-spacing: _size(letter-spacing-alt);
				text-transform: uppercase;
				top: 0;

				a {
					color: inherit;
				}
			}
		}
	}

	#minihead {
		> header {
			@include padding(1em, 1em);
			background: _palette(accent7, bg);
			text-align: left;

			h1, h2 {
				font-size: _font(hdr-size-xxlarge);
				margin: 0;
				color: _palette(accent7, fg);
			}

			p {
				color: _palette(bg);
				letter-spacing: _size(letter-spacing-alt);
				text-transform: uppercase;
				top: 0;

				a {
					color: inherit;
				}
			}
		}
	}

	#main {
		> header {
			@include padding(12em, 0);
			@include vendor('background-image', ('linear-gradient(top, rgba(0,0,0,0.3), rgba(0,0,0,0.3))', 'url("#{$baseurl}/banners/banner.jpg")'));
			background-attachment: fixed;
			background-position: center center;
			background-repeat: no-repeat;
			background-size: cover;
			text-align: center;

			h1, h2 {
				font-size: _font(hdr-size-xxlarge);
				margin: 0 0 (_size(element-margin) * 0.25) 0;
			}

			p {
				color: inherit;
				letter-spacing: _size(letter-spacing-alt);
				text-transform: uppercase;
				top: 0;

				a {
					color: inherit;
				}
			}

			@include breakpoint(xlarge) {
				@include padding(8em, 0);
			}

			@include breakpoint(large) {
				@include padding(8em, 3em);
			}

			@include breakpoint(medium) {
				@include padding(6em, 3em);
			}

			@include breakpoint(small) {
				@include padding(3em, 3em);

				h1, h2 {
					font-size: _font(hdr-size-mlarge);
					margin: 0 0 (_size(element-margin) * 0.5) 0;
				}
			}
		}
	}

	body.is-mobile {
		#main {
			> header {
				background-attachment: scroll;
			}
		}
	}

/* Footer */

	#footer {
		@include padding(6em, 0);
/*		background-color: darken(_palette(bg), 8);*/
	// background-color: black;
	background-color: rgba(30, 32, 36, 0.75);

		text-align: center;

		.icons {
			font-size: 1.5em;

			a {
				color: _palette(fg-light);

				&:hover {
					color: _palette(fg);
				}
			}
		}

		.copyright {
			color: _palette(fg-light);
			font-size: 0.8em;
			letter-spacing: _size(letter-spacing-alt);
			list-style: none;
			padding: 0;
			text-transform: uppercase;

			li {
				border-left: solid 1px _palette(fg-light);
				display: inline-block;
				line-height: 1em;
				margin-left: 1em;
				padding-left: 1em;

				&:first-child {
					border-left: 0;
					margin-left: 0;
					padding-left: 0;
				}

				a {
					color: inherit;

					&:hover {
						color: _palette(fg);
					}
				}

				@include breakpoint(xsmall) {
					border: 0;
					display: block;
					line-height: 1.65em;
					margin: 0;
					padding: 0.5em 0;
				}
			}
		}

		@include breakpoint(medium) {
			@include padding(4em, 3em);
		}

		@include breakpoint(small) {
			@include padding(3em, 2em);
		}
	}

/* Landing */

	body.landing {
		#page-wrapper {
			@include vendor('background-image', ('linear-gradient(top, rgba(0,0,0,0.3), rgba(0,0,0,0.3))', 'url("#{$baseurl}/banners/banner.jpg")'));
			background-attachment: fixed;
			background-position: center center;
			background-repeat: no-repeat;
			background-size: cover;
			padding-top: 0;

		}

		#footer {
		/*background-color: darken(transparentize(_palette(bg), 0.1), 8);*/
			background-color: darken(transparentize(black, 0.1), 8);
		}
	}

	body.is-mobile {
		&.landing {
			#page-wrapper {
				background: none;
			}

			#banner,
			.wrapper.style4 {
				@include vendor('background-image', ('linear-gradient(top, rgba(0,0,0,0.3), rgba(0,0,0,0.3))', 'url("#{$baseurl}/banners/banner.jpg")'));
				background-position: center center;
				background-repeat: no-repeat;
				background-size: cover;
			}

			#footer {
				background-color: darken(_palette(bg), 8);
			}
		}
	}

/* font variant */
